<template>
  <div class="card">
    <div class="card-image p-5">
      <b-image alt="Player Picture" src="https://raw.githubusercontent.com/zcore-coin/zefi-branding/main/LogoTextNewWhite.png" :responsive="true" :rounded="false"></b-image>
    </div>
    <div class="card-content">
      <b-field position="is-centered">
        <b-input v-model="WalletAddress" placeholder="Wallet Address..." type="text" icon="hexadecimal" expanded> </b-input>
        <p class="control">
          <b-button label="Start" type="is-dark" @click="start" />
        </p>
      </b-field>
      <b-message :title="pool.token ? pool.token.symbol : pool.token0.symbol + '-' + pool.token1.symbol" type="is-dark" v-for="(pool, index) in poolsArray" v-bind:key="index" :closable="false">
        <div class="table-container">
          <table class="table is-bordered is-striped is-narrow is-fullwidth">
            <thead>
              <tr>
                <th colspan="2">Liquidity Pool Information</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Address:</td>
                <td>
                  <a target="_blank" :href="'https://bscscan.com/address/' + pool.info.lpToken">
                    {{ pool.info.lpToken }}
                  </a>
                </td>
              </tr>
              <tr>
                <td>Allocated Points (Multiplier):</td>
                <td>{{ pool.info.allocPoint / 100 }}x</td>
              </tr>
              <tr>
                <td>Deposit Fee:</td>
                <td>{{ pool.info.depositFeeBP / 100 }}%</td>
              </tr>
              <tr>
                <td>Total Supply:</td>
                <td>{{ (pool.info.totalSupply / 1e18).toFixed(4) }}</td>
              </tr>
              <tr>
                <td>Accumulated ZeFi Per Share:</td>
                <td>{{ parseFloat(web3.utils.fromWei(pool.info.accZeFiPerShare, "Ether")).toFixed(8) }}</td>
              </tr>
              <tr>
                <td>Last Reward Block:</td>
                <td>{{ pool.info.lastRewardBlock }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div v-if="pool.token">
          <b-table :data="[pool.token]" bordered striped narrowed>
            <b-table-column field="symbol" label="Symbol" centered v-slot="props">
              {{ props.row.symbol }}
            </b-table-column>
            <b-table-column field="totalSupply" label="Total Supply" numeric v-slot="props">
              {{ Math.round(props.row.totalSupply / 1e18) }}
            </b-table-column>
            <b-table-column field="decimals" label="Decimals" numeric v-slot="props">
              {{ props.row.decimals }}
            </b-table-column>
            <b-table-column field="price" label="Price" numeric v-slot="props"> ${{ (props.row.price / 1e18).toFixed(2) }} </b-table-column>
            <b-table-column field="balanceOf" label="My Balance" numeric v-slot="props">
              {{ (props.row.balanceOf / 1e18).toFixed(2) }}
            </b-table-column>
          </b-table>
        </div>
        <div v-if="pool.token0">
          <b-table :data="[pool.token0, pool.token1]" bordered striped narrowed>
            <b-table-column field="symbol" label="Symbol" centered v-slot="props">
              {{ props.row.symbol }}
            </b-table-column>
            <b-table-column field="reserves" label="Reserves" numeric v-slot="props">
              {{ (props.row.reserves / 1e18).toFixed(4) }}
            </b-table-column>
            <b-table-column field="reservesPrice" label="Reserves Price" numeric v-slot="props"> ${{ (props.row.reservesPrice / 1e18 / 1e18).toFixed(2) }} </b-table-column>
            <b-table-column field="totalSupply" label="Total Supply" numeric v-slot="props">
              {{ Math.round(props.row.totalSupply / 1e18) }}
            </b-table-column>
            <b-table-column field="decimals" label="Decimals" numeric v-slot="props">
              {{ props.row.decimals }}
            </b-table-column>
            <b-table-column field="price" label="Price" numeric v-slot="props"> ${{ (props.row.price / 1e18).toFixed(2) }} </b-table-column>
            <b-table-column field="balanceOf" label="My Balance" numeric v-slot="props">
              {{ (props.row.balanceOf / 1e18).toFixed(2) }}
            </b-table-column>
          </b-table>
        </div>
        <ul>
          <li>Total LP Supply Price: ${{ parseFloat(web3.utils.fromWei(pool.totalLpSupplyPrice, "Ether")).toFixed(2) }}</li>
          <li>1 LP Token Price: ${{ pool.oneLpPrice.toFixed(2) }}</li>
        </ul>
        <table class="table is-bordered is-striped is-narrow is-fullwidth">
          <thead>
            <tr>
              <th colspan="2">Master Chef Information</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Balance of LP Tokens:</td>
              <td>{{ parseFloat(web3.utils.fromWei(pool.masterChef.balanceOf, "Ether")).toFixed(4) }}</td>
            </tr>
            <tr v-if="pool.masterChef.pairQuantity">
              <td>Pair Quantity</td>
              <td>
                {{ pool.masterChef.pairQuantity.token0.toFixed(4) }}
                {{ pool.token0.symbol }}
                +
                {{ pool.masterChef.pairQuantity.token1.toFixed(4) }}
                {{ pool.token1.symbol }}
              </td>
            </tr>
            <tr>
              <td>LP Tokens Total Price:</td>
              <td>${{ pool.masterChef.balanceInDollar.toFixed(2) }}</td>
            </tr>
            <tr>
              <td>Percentage of LP Tokens:</td>
              <td>{{ (pool.masterChef.percentageOfLp * 100).toFixed(2) }}%</td>
            </tr>
            <tr>
              <td>Reward Percentage:</td>
              <td>{{ (pool.masterChef.rewardPercentage * 100).toFixed(2) }}%</td>
            </tr>
            <tr>
              <td>Reward per Day:</td>
              <td>{{ pool.masterChef.rewardPerDay.toFixed(2) }}</td>
            </tr>
            <tr>
              <td>Reward per Day in Dollar:</td>
              <td>${{ pool.masterChef.rewardPerDayInDollar.toFixed(2) }}</td>
            </tr>
            <tr>
              <td>APR per Day:</td>
              <td>{{ (pool.masterChef.aprPerDay * 100).toFixed(2) }}%</td>
            </tr>
            <tr>
              <td>APR per Month:</td>
              <td>{{ (pool.masterChef.aprPerDay * 30 * 100).toFixed(2) }}%</td>
            </tr>
            <tr>
              <td>APR per Year:</td>
              <td>{{ (pool.masterChef.aprPerDay * 365 * 100).toFixed(2) }}%</td>
            </tr>
          </tbody>
        </table>

        <table class="table is-bordered is-striped is-narrow is-fullwidth">
          <thead>
            <tr>
              <th colspan="2">User Information</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Amount Staked:</td>
              <td>{{ parseFloat(web3.utils.fromWei(pool.user.amount, "Ether")).toFixed(2) }}</td>
            </tr>
            <tr v-if="pool.masterChef.pairQuantity">
              <td>Pair Quantity</td>
              <td>
                {{ (pool.masterChef.pairQuantity.token0 * pool.user.percentageOfLpInMasterChef).toFixed(4) }}
                {{ pool.token0.symbol }}
                +
                {{ (pool.masterChef.pairQuantity.token1 * pool.user.percentageOfLpInMasterChef).toFixed(4) }}
                {{ pool.token1.symbol }}
              </td>
            </tr>
            <tr>
              <td>Amount Staked Price:</td>
              <td>${{ pool.user.amountPrice.toFixed(2) }}</td>
            </tr>
            <tr>
              <td>Reward Debt:</td>
              <td>{{ parseFloat(web3.utils.fromWei(pool.user.rewardDebt, "Ether")).toFixed(2) }}</td>
            </tr>
            <tr>
              <td>Percentage of LP in MasterChef</td>
              <td>{{ (pool.user.percentageOfLpInMasterChef * 100).toFixed(2) }}%</td>
            </tr>
            <tr>
              <td>Rewards per Day</td>
              <td>{{ pool.user.rewardPerDay.toFixed(2) }}</td>
            </tr>
            <tr>
              <td>Rewards per Day in Dollar</td>
              <td>${{ pool.user.rewardPerDayInDollar.toFixed(2) }}</td>
            </tr>
          </tbody>
        </table>
      </b-message>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import RoutePancakeSwapV2ABI from "@/abi/RoutePancakeSwapV2.json";
import FactoryPancakeSwapV2ABI from "@/abi/FactoryPancakeSwapV2.json";
import MasterChefZefiABI from "@/abi/MasterChefZefi.json";
import PairLpPancakeABI from "@/abi/PairLpPancake.json";

const RoutePancakeSwapV2Address = "0x10ED43C718714eb63d5aA57B78B54704E256024E";
const FactoryPancakeSwapV2Address = "0xcA143Ce32Fe78f1f7019d7d551a6402fC5350c73";
const FactoryPancakeSwapV1Address = "0xBCfCcbde45cE874adCB698cC183deBcF17952812";
const MasterChefZefiAddress = "0x05a8ba2DEf87f8AdaF223Edcd04F7Fc82eA6aee8";
const RewardTokenAddress = "0x0288D3E353fE2299F11eA2c2e1696b4A648eCC07";
const WBNB_TOKEN = "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c";
const BUSD_TOKEN = "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56";
const USDT_TOKEN = "0x55d398326f99059ff775485246999027b3197955";

const ONE_UNIT = "1000000000000000000";
const BSC_BLOCKS_PER_DAY = 28800;

export default {
  name: "Zcore",
  data() {
    return {
      WalletAddress: '',
      web3: new this.$web3("https://bsc-dataseed1.binance.org:443"), // BSC MainNet
      routePancakeSwapV2Contract: {},
      prices: new Map(),
      tokenInfo: new Map(),
      masterChef: {},
      pools: new Map(),
      poolsArray: [],
      BN: {},
    };
  },
  computed: {
    // poolsArray() {
    //   console.log(this.pools.size);
    //   console.log(this.pools.entries());
    //   return Array.from(this.pools.values());
    // },
  },
  methods: {
    async coinInfo(coinAddress) {
      if (!this.tokenInfo.has(coinAddress)) {
        let coinInfo = {};
        const pairLpPancakeContract = new this.web3.eth.Contract(PairLpPancakeABI, coinAddress);
        coinInfo.totalSupply = await pairLpPancakeContract.methods.totalSupply().call();
        coinInfo.decimals = await pairLpPancakeContract.methods.decimals().call();
        coinInfo.balanceOf = await pairLpPancakeContract.methods.balanceOf(this.WalletAddress).call();
        coinInfo.symbol = await pairLpPancakeContract.methods.symbol().call();
        coinInfo.price = await this.getCoinPriceInBusd(coinAddress);
        // console.log("BUSD Price:", this.web3.utils.fromWei(coinInfo.price , "Ether"));
        this.tokenInfo.set(coinAddress, coinInfo);
      }

      return this.tokenInfo.get(coinAddress);
    },
    async getCoinPriceInBusd(coinAddress) {
      if (!this.prices.has(coinAddress)) {
        let coinPrice = 0;
        try {
          let coinPriceInBusd = await this.routePancakeSwapV2Contract.methods.getAmountsOut(ONE_UNIT, [coinAddress, BUSD_TOKEN]).call();
          coinPrice = coinPriceInBusd[1];
        } catch (error) {
          let coinPriceInWbnb = await this.routePancakeSwapV2Contract.methods.getAmountsOut(ONE_UNIT, [coinAddress, WBNB_TOKEN]).call();
          let result = new this.BN(coinPriceInWbnb[1]).mul(new this.BN(this.prices.get(WBNB_TOKEN))).div(new this.BN(ONE_UNIT));
          coinPrice = result.toString();
        }

        this.prices.set(coinAddress, coinPrice);
      }

      return this.prices.get(coinAddress);
    },
    async start() {
      let loadingComponent = this.$buefy.loading.open();
      this.BN = this.web3.utils.BN;
      this.routePancakeSwapV2Contract = new this.web3.eth.Contract(RoutePancakeSwapV2ABI, RoutePancakeSwapV2Address);
      await this.getCoinPriceInBusd(WBNB_TOKEN); // Store WBNB Price for future calculations
      let rewardTokenPrice = await this.getCoinPriceInBusd(RewardTokenAddress);
      rewardTokenPrice = rewardTokenPrice / 1e18;
      // console.log("rewardTokenPrice:", rewardTokenPrice);

      // const currentBlockNumber = await this.web3.eth.getBlockNumber();
      // console.log("currentBlockNumber:", currentBlockNumber);

      // const gasPrice = await this.web3.eth.getGasPrice();
      // console.log("getGasPrice in Gwai:", this.web3.utils.fromWei(gasPrice, "Gwei"));

      // const walconstBalance = await this.web3.eth.getBalance(this.WalletAddress);
      // console.log("walconstBalance: ", walconstBalance / 1e18);
      // const walconstTxCount = await this.web3.eth.getTransactionCount(this.WalletAddress);
      // console.log("walconstTxCount: ", walconstTxCount);

      // const factoryPancakeSwapV2Contract = new this.web3.eth.Contract(FactoryPancakeSwapV2ABI, FactoryPancakeSwapV2Address);
      const masterChefZefiContract = new this.web3.eth.Contract(MasterChefZefiABI, MasterChefZefiAddress);

      // console.log("wbnbPriceInBusd: ", this.wbnbPrice, this.web3.utils.fromWei(this.wbnbPrice, "Ether"));

      // console.log(masterChefZefiContract.methods);

      // console.log('getMultiplier:', await masterChefZefiContract.methods.getMultiplier(currentBlockNumber - 100, currentBlockNumber).call());
      // console.log('BONUS_MULTIPLIER:', await masterChefZefiContract.methods.BONUS_MULTIPLIER().call());
      // const zefiTokenAddress = await masterChefZefiContract.methods.zefi().call();
      // console.log("zefiTokenAddress:", zefiTokenAddress);
      const zefiPerBlock = await masterChefZefiContract.methods.zefiPerBlock().call();
      const zefiPerDay = (zefiPerBlock * BSC_BLOCKS_PER_DAY) / 1e18;
      // console.log("zefiPerBlock:", zefiPerBlock / 1e18);
      // console.log("zefi per day:", zefiPerDay);
      // const startBlock = await masterChefZefiContract.methods.startBlock().call();
      // console.log("startBlock:", startBlock);
      const totalAllocPoint = await masterChefZefiContract.methods.totalAllocPoint().call();
      // console.log("totalAlloationcPoints:", totalAllocPoint);
      // const poolLength = await masterChefZefiContract.methods.poolLength().call();
      // console.log("poolLength:", poolLength);

      this.masterChef = {
        tokenAddress: await masterChefZefiContract.methods.zefi().call(),
        tokenPerBlock: await masterChefZefiContract.methods.zefiPerBlock().call(),
        startBlock: await masterChefZefiContract.methods.startBlock().call(),
        totalAlloationcPoints: await masterChefZefiContract.methods.totalAllocPoint().call(),
        poolLength: await masterChefZefiContract.methods.poolLength().call(),
      };
      // console.log(this.masterChef);

      for (let i = 0; i < this.masterChef.poolLength; i++) {
      // for (const i of [14, 66, 67, 68]) {
        // console.log(`---------------- POOL: ${i} ----------------------`);
        let pool = {};
        const poolInfo = await masterChefZefiContract.methods.poolInfo(i).call();
        // const pollInfoObj = {
        //   lpTokenAddress: poolInfo.lpToken, // Address of LP token contract.
        //   allocationPointsToThisPool: poolInfo.allocPoint, // How many allocation points assigned to this pool. ZEFIs to distribute per block.
        //   lastRewardBlock: poolInfo.lastRewardBlock, // Last block number that ZEFIs distribution occurs.
        //   accumulatedZeFiPerShare: poolInfo.accZeFiPerShare, // Accumulated ZEFIs per share, times 1e12. See below.
        //   depositFee: poolInfo.depositFeeBP, // Deposit fee in basis points
        // };
        // console.log(`poolInfo:`, pollInfoObj);
        pool.info = poolInfo;
        pool.masterChef = {};

        pool.user = await masterChefZefiContract.methods.userInfo(i, this.WalletAddress).call();

        // If no balance,  skip
        if(pool.user.amount == '0') {
          continue;
        }

        // console.log(`userInfo:`, userInfoObj);

        // const pendingZefi = await masterChefZefiContract.methods.pendingZeFi(i, this.WalletAddress).call();
        // console.log(`pendingZefi:`, pendingZefi / 1e18);

        // console.log('poolInfo.lpToken', poolInfo.lpToken);
        const pairLpPancakeContract = new this.web3.eth.Contract(PairLpPancakeABI, poolInfo.lpToken);
        const name = await pairLpPancakeContract.methods.name().call();
        pool.info.totalSupply = await pairLpPancakeContract.methods.totalSupply().call();

        // @TODO pegando o preÃ§o errado do ZEFI: 0x0288D3E353fE2299F11eA2c2e1696b4A648eCC07
        pool.masterChef.balanceOf = await pairLpPancakeContract.methods.balanceOf(MasterChefZefiAddress).call();
        // console.log(`pool.masterChef.balanceOf`, pool.masterChef.balanceOf, MasterChefZefiAddress);
        pool.masterChef.percentageOfLp = pool.masterChef.balanceOf / pool.info.totalSupply;
        pool.masterChef.rewardPercentage = pool.info.allocPoint / this.masterChef.totalAlloationcPoints;
        // pool.masterChef.rewardPerDay = zefiPerDay * pool.masterChef.rewardPercentage * pool.masterChef.percentageOfLp;
        pool.masterChef.rewardPerDay = zefiPerDay * pool.masterChef.rewardPercentage;
        pool.user.percentageOfLpInMasterChef = pool.user.amount / pool.masterChef.balanceOf;
        pool.user.rewardPerDay = pool.masterChef.rewardPerDay * pool.user.percentageOfLpInMasterChef;
        pool.user.rewardPerDayInDollar = pool.user.rewardPerDay * rewardTokenPrice;
        // console.log(pool.user);
        // console.log("name:", name);
        if (name == "Pancake LPs") {
          const lpReserves = await pairLpPancakeContract.methods.getReserves().call();
          const token0 = await pairLpPancakeContract.methods.token0().call();
          let info0 = await this.coinInfo(token0);
          const token1 = await pairLpPancakeContract.methods.token1().call();
          let info1 = await this.coinInfo(token1);
          let token0Obj = {
            ...info0,
            reserves: lpReserves._reserve0,
            reservesPrice: new this.BN(lpReserves._reserve0).mul(new this.BN(info0.price)).toString(),
          };
          let token1Obj = {
            ...info1,
            reserves: lpReserves._reserve1,
            reservesPrice: new this.BN(lpReserves._reserve1).mul(new this.BN(info1.price)).toString(),
          };

          // let lpPriceTotal = new this.BN(token0Obj.reservesPrice).add(new this.BN(token1Obj.reservesPrice)).toString();
          // console.log(token0Obj, token1Obj, lpPriceTotal / 1e18 / 1e18);

          pool.info = poolInfo;
          pool.lpReserves = lpReserves;
          pool.token0 = token0Obj;
          pool.token1 = token1Obj;
          pool.totalLpSupplyPrice = new this.BN(pool.token0.reservesPrice).add(new this.BN(pool.token1.reservesPrice)).div(new this.BN(ONE_UNIT)).toString();
          // console.log(token0Obj.reserves);
          // console.log(pool.masterChef.percentageOfLp);
          pool.masterChef.pairQuantity = {
            token0: (token0Obj.reserves / 1e18) * pool.masterChef.percentageOfLp,
            token1: (token1Obj.reserves / 1e18) * pool.masterChef.percentageOfLp,
          };
        } else {
          pool.token = await this.coinInfo(poolInfo.lpToken);
          // console.log(pool.token);
          pool.totalLpSupplyPrice = new this.BN(pool.token.totalSupply).mul(new this.BN(pool.token.price)).div(new this.BN(ONE_UNIT)).toString();
        }

        pool.oneLpPrice = pool.totalLpSupplyPrice / pool.info.totalSupply;
        pool.user.amountPrice = (pool.user.amount * pool.oneLpPrice) / 1e18;

        pool.masterChef.balanceInDollar = (pool.masterChef.balanceOf / 1e18) * pool.oneLpPrice;
        pool.masterChef.rewardPerDayInDollar = pool.masterChef.rewardPerDay * rewardTokenPrice;
        pool.masterChef.aprPerDay = pool.masterChef.rewardPerDayInDollar / pool.masterChef.balanceInDollar;

        this.pools.set(i, pool);
        // console.log(pool.masterChef);
      }

      // console.log(this.prices);
      // console.log(this.tokenInfo);
      // console.log(this.pools.values());
      this.poolsArray = Array.from(this.pools.values());

      loadingComponent.close();
    },
  },
  async created() {
    // console.log("=============created===============");
  },
};
</script>